nav_container{
  padding: 10px;
}

nav {
  background: rgba(0, 0, 0, 0.3);
  width: max-content;
  display: inline-block;
  padding: 0.7rem 1.8rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 1.5rem;
  display: flex;
  flex-direction: row;
  flex-shrink: inherit;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
}

nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-light);
  font-size: 1.1rem;
}

nav a:hover {
  background: rgba(0, 0, 0, 0.3);
}

nav a.active {
  background: var(--color-primary);
  color: var(--color-bg);
}

@media screen and (max-width: 1024px) {
  nav {
    padding: 0.9rem;
  }
}

/* ======= MEDIA QUERIES (SMALL DEVICES) =======*/
@media screen and (max-width: 400px) {
  nav {
    width: min-content;
    padding: 0.9rem;
  }
}
